<template>
  <v-container fluid pt-0>
    <v-layout justify-center>
      <v-flex xs12 sm12 md9 lg6>
        <v-layout column>
          <v-flex mb-3 bw>
            <!-- <search
              label="Search viruses, bacteria, protozoa, helminths"
              placeholder="Start typing to search"></search> -->
            <v-text-field
              v-model="searchPathogen"
              label="Search viruses, bacteria, protozoa, helminths"
              placeholder="Start typing to search"
              :append-icon="inputIcon"
              @click:append="searchPathogen = ''"
              outline
            ></v-text-field>
          </v-flex>
          <v-flex
            v-for="microbe in filteredPathogens"
            :key="microbe.pathogenId"
            mb-3
            pa-2
            card
            @click="goto(microbe.pathogenId)"
            pcoh
          >
            <v-layout justify-space-between align-center>
              <v-avatar size="24" color="grey lighten-4">
                <img :src="pathogenUrl(microbe.type)" alt="avatar" />
              </v-avatar>
              <v-flex xs12 pl-3>
                <span class="subheading font-weight-light">{{
                  microbe.pathogenName
                }}</span>
              </v-flex>
              <v-flex pl-4>
                <v-icon>keyboard_arrow_right</v-icon>
              </v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  created() {
    this.getPathogens(1);
  },
  computed: {
    ...mapGetters(["pathogens"]),
    filteredPathogens() {
      return this.pathogens.filter(pathogen =>
        pathogen.pathogenName
          .toLowerCase()
          .includes(this.searchPathogen.toLowerCase())
      );
    },
    inputIcon() {
      return this.searchPathogen !== "" ? "clear" : "search";
    }
  },
  data() {
    return {
      searchPathogen: ""
    };
  },
  methods: {
    ...mapActions(["getPathogens"]),
    goto(link) {
      this.$router.push(`/pathogens/${link}`);
    },
    pathogenUrl(kind) {
      switch (kind) {
        case "virus":
          return "https://img.scoop.it/6Tvnpt9ZkSop-G9oSN5Objl72eJkfbmt4t8yenImKBVvK0kTmF0xjctABnaLJIm9";
        case "fungi":
          return "https://upload.wikimedia.org/wikipedia/commons/thumb/3/34/Candida_pap_1.jpg/220px-Candida_pap_1.jpg";
        case "protozoa":
          return "https://katekearapelen.files.wordpress.com/2010/03/1760532740_20189a8cdc.jpg";
        case "rickettsia":
          return "https://upload.wikimedia.org/wikipedia/commons/thumb/8/86/Rickettsia_rickettsii.jpg/220px-Rickettsia_rickettsii.jpg";
        case "helminth":
          return "https://www.infectiousdisease.cam.ac.uk/images/Schistomsoma%20mansoni.jpg/image_mini";
        default:
          return "https://previews.123rf.com/images/frenta/frenta1611/frenta161100123/66972770-pathogen-bacteria-on-the-surface-3d-render.jpg";
      }
    }
  }
};
</script>

